body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  position: relative;
  height: 100%;
  width: 100%; }

.app-container {
  position: relative;
  height: 100%;
  width: 100%; }
  .app-container .main-container {
    height: calc(100% - 60px);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start; }
    .app-container .main-container .app-aside {
      width: 180px;
      height: 100%; }
      .app-container .main-container .app-aside .aside-container {
        width: 180px;
        height: 100%; }
    .app-container .main-container .app-content {
      width: calc(100% - 180px);
      height: 100%;
      padding: 20px; }
      .app-container .main-container .app-content .todo-container .todo-list-box > li {
        height: 30px;
        line-height: 30px;
        margin-top: 5px;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        -webkit-align-items: center;
                align-items: center; }
        .app-container .main-container .app-content .todo-container .todo-list-box > li .item-dot,
        .app-container .main-container .app-content .todo-container .todo-list-box > li .item-index {
          display: inline-block;
          height: 100%; }
        .app-container .main-container .app-content .todo-container .todo-list-box > li .item-index {
          margin: 0 3px; }
        .app-container .main-container .app-content .todo-container .todo-list-box > li .anticon {
          margin: 0 10px;
          font-size: 18px;
          color: red; }
        .app-container .main-container .app-content .todo-container .todo-list-box > li .ant-input {
          display: inline-block;
          margin-right: 20px;
          width: 300px;
          height: 100%; }
      .app-container .main-container .app-content .todo-container .add-box {
        display: -webkit-flex;
        display: flex;
        height: 40px;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        -webkit-align-items: center;
                align-items: center; }
        .app-container .main-container .app-content .todo-container .add-box .add-input {
          display: inline-block;
          width: 300px; }
      .app-container .main-container .app-content .todo-container .todo-name {
        display: inline-block;
        margin-right: 20px;
        width: 300px;
        text-overflow: ellipsis;
        overflow: hidden; }

.app-header {
  height: 60px;
  background: #1890ff;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }
  .app-header .app-logo {
    font-size: 24px; }

